.widgetSm{
    flex:1;
    box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75);
    padding:20px;
    margin-right:20px;
}
.widgetSmTitle{
    font-size:22px;
    font-weight:600;
}
.widgetSmImg{
    width:40px;
    height:40px;
    border-radius:50%;
    object-fit:cover;
}
.widgetSmList{
    margin:0;
    padding:0;
    list-style:none;
}
.widgetSmListItem{
    display:flex;
    align-items: center;
    justify-content:space-between;
    margin:20px 0;
}
.widgetSmUser{
    display:flex;
    flex-direction:column;
}
.widgetSmButton{
    display:flex;
    align-items: center;
    border: none;
    border-radius:10px;
    padding:7px 10px;
    background-color:#eeeef7;
    color:#555;
    cursor: pointer;
}
.widgetSmIcon{
    font-size:16px;
    margin-right:5px;
}